@charset "UTF-8";
* {
  box-sizing: content-box;
  outline: none;
}

html {
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
}

:root {
  --dark: #1D274B;
  --text: #3D4871;
  --dark-text: #323A56;
  --text-alt: #ffffff;
  --grey: #6A77A6;
  --border: #CFD6E8;
  --border-alt: #E2EAF0;
  --bg: #F1F5F8;
  --bg-alt: #ffffff;
  --blue: #1E5BA6;
  --dark-blue: #2A4BBF;
  --blue-hover: #0C4C9B;
  --blue-active: #072DB4;
  --soft-blue: #9CB1F9;
  --light-blue: #DCE3F5;
  --active-blue: #263E93;
  --very-light-blue: #C2D0FF;
  --purple: #4F6CD4;
  --orange: #FB9E30;
  --orange-hover: #EF8509;
  --soft-orange: #FFC785;
  --very-soft-orange: #FFECD6;
  --active-orange: #FFDAAF;
  --dark-orange: #E47B00;
  --red: #F34F4F;
  --soft-red: #EB7171;
  --light-red: #FFE2E2;
  --green: #2FA01C;
  --soft-green: #7DD06F;
  --light-green: #E4FFE0;
  --bright-green: #55BD44;
  --great-transition: all 200ms ease;
  font-size: 10px;
  line-height: 1.36;
  color: var(--text);
  font-family: "Nunito", sans-serif;
}

body {
  position: relative;
  flex-direction: column;
  display: flex;
  background-color: #fff;
  margin: 0 auto;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  min-height: 100vh;
  margin: 0 auto;
  padding-left: 280px;
  background-color: var(--bg);
  color: var(--text);
  font-size: 1.5rem;
  font-weight: 500;
}

#footer {
  width: 100%;
  box-sizing: border-box;
  position: relative;
  margin-top: auto;
}

h1, h2, h3, h4, h5, h6, p {
  display: block;
  margin: 0;
  padding: 0;
  font-weight: 400;
}

.h1, .h2, .h3, .h4, .h5, .h6 {
  margin: 0;
  padding: 0;
  float: none;
}

img, input, textarea, button, a {
  border: none;
  outline: none;
}

input, textarea, button, a {
  font-family: "Nunito", sans-serif;
  font-weight: normal;
  font-size: 1.11rem;
  line-height: 1;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  margin: 0;
  padding: 0;
}

td, th {
  vertical-align: top;
  word-wrap: break-word;
}

ul, ol, nav, dl {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  font-size: inherit;
  line-height: inherit;
  color: var(--blue);
  font-weight: inherit;
}

a:hover {
  text-decoration: underline;
}

button {
  display: block;
  cursor: pointer;
  box-sizing: border-box;
  padding: 0;
}

button, .button {
  text-decoration: none !important;
}

address {
  font-style: normal;
}

.inner {
  position: relative; /*max-width:1545px;*/
  width: 96%;
  margin: 0 auto;
  box-sizing: border-box;
}

section {
  position: relative;
}

main {
  position: relative;
  clear: both;
  padding-top: 4rem;
  box-sizing: border-box;
}

header, nav, section, article, aside, footer, main {
  display: block;
}

fieldset {
  position: relative;
  border: 0;
}

legend {
  font-size: 0;
  position: absolute;
  width: 1px;
  height: 1px;
  opacity: 0;
}

.cover {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.contain {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
}

.no-text {
  font-size: 0 !important;
  color: transparent;
  background-color: rgba(255, 255, 255, 0.504);
}

.fleft {
  float: left;
}

.fright {
  float: right;
}

.db {
  display: block;
}

.inb {
  display: inline-block;
}

.in {
  display: inline;
}

.tb {
  display: table;
}

.tbc {
  display: table-cell;
}

.tbr {
  display: table-row-group;
}

.tr {
  display: table-row;
}

.on_dekstop {
  display: block;
}

.on_mobile {
  display: none;
}

.hide {
  display: none;
}

.hideI {
  display: none !important;
}

.w20 {
  width: 20%;
}

.w25 {
  width: 25%;
}

.w30 {
  width: 30%;
}

.w33 {
