}

.alCenter {
  text-align: center;
}

.alRight {
  text-align: right;
}

.alLeft {
  text-align: left;
}

.cp {
  cursor: pointer;
}

.cd {
  cursor: default;
}

.ca {
  cursor: auto;
}

.zt200 {
  z-index: 200;
}

.box {
  box-sizing: border-box;
}

.ell {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.bgn {
  background: none !important;
}

.row {
  margin: 0;
  float: none;
}

.b0 {
  bottom: 0;
}

.t0 {
  top: 0;
}

.r0 {
  right: 0;
}

.l0 {
  left: 0;
}

.up {
  text-transform: uppercase;
}

.low {
  text-transform: lowercase;
}

.fsn {
  font-style: normal;
}

.pb0 {
  padding-bottom: 0;
}

.upp {
  text-transform: uppercase;
}

.spacer {
  display: block;
  width: 100%;
  height: auto;
}

.clearfix::after, .clear::after {
  content: "";
  display: table;
  width: 100%;
  clear: both;
}

.placeholder::-webkit-input-placeholder {
  opacity: 1;
  transition: opacity 0.3s ease;
  color: var(--grey);
}

