.burger::before {
  content: "";
  position: absolute;
  top: 1.1rem;
  left: 0.5rem;
  width: 50%;
  height: 2px;
  background-color: var(--blue);
  transition: var(--great-transition);
}
.burger::after {
  content: "";
  position: absolute;
  bottom: 1.1rem;
  right: 0.5rem;
  width: 50%;
  height: 2px;
  background-color: var(--blue);
  transition: var(--great-transition);
}
.burger--active span {
  opacity: 0;
}
.burger--active::before {
  top: 50%;
  left: 50%;
  width: 80%;
  height: 2px;
  transform: translate(-50%, -50%) rotate(-45deg);
}
.burger--active::after {
  bottom: 50%;
  right: 50%;
  width: 80%;
  height: 2px;
  transform: translate(50%, 50%) rotate(45deg);
}

